@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  outline: none;
  list-style: none;
  font-family: "outfit", sans-serif;
  font-optical-sizing: auto;
  font-weight: weight;
  font-style: normal;
}

body {
  background: #f1f2f6;
  margin-top: 4rem;
  height: 1000px;
}

.container {
  padding-left: 2rem;
  padding-right: 2rem;
}

.btn {
  background: white;
  color: black;
  /* padding: 14px 25px; */
  font-size: 16px;
  border-radius: 30px;
  cursor: pointer;
  border: 0;
  width: 100px;
  padding-top: 5px;
  padding-bottom: 5px;
  
}

